#listItem{
    color: inherit;
    text-decoration: inherit;
    
}

header{
  
    color:white;
   
}
.logo{
}
#header {
    background-color: rgba(24, 27, 41, 1);
    padding-block: 2em;

}

#topHeader{
   background-color: rgba(24, 27, 41, 0.8);
    color: white;
   
}

.container,.row
{
    width: 95%;
    margin-inline: auto;
    display: flex;
    justify-content: space-between;
    position: relative;

}


.headerCartWishlistCtn{
    display: flex;
    gap: 1em;

    
}

.ul{
    
    list-style: none;
}
.list{
    display: flex;
    gap: 1rem;
}

.categoryList{
    height: 3.5em;
    border-radius: 25px 0px 0px 25px;
    width: 9em;
    background-color: white;
    border-width: 0px;
}
#searchBtn{
    height: 3.5em;
    background-color: blue;
    border-radius: 0px 25px  25px 0px;
    width: 8em;
    border-width: 0px;
    color: white;
    
}
.inputArea{
    height: 3.3em;
    width: 25em;
    border-width: 0px;
    border-left-width:2px;
    border-color: rgba(255, 255, 255, 0.5);
}
.headerSearchContainer, .headerCartWishlistCtnContainer{
  
    height: fit-content;
    margin-top: auto;
    margin-bottom: auto;

}
.cartContainer, .wishListContainer{
    width: 100%;
    font-size: 10px;
    
}

#cart, .cartMessage, #wishlistIcon{
    margin-inline:auto;
    width: fit-content;
}
#cartItemCount{
    position: absolute;
    top: 2%;
    right: 3%;
    width: 20px;
    height: 20px;
    border-radius: 9px 9px 9px 9px;
    background-color: rgba(255, 0, 0, 0.9);
   
    font-size: 11px;
    font-weight: bold;
    
}
#qty{
    width: fit-content;
    height: fit-content;
    margin-top: 2px;
    margin-inline: auto;
}
#wishlistIcon{

}
#wishlistCount{
    position: absolute;
}
#wishlistQty{

}
.wishListMessage{
    
    width: max-content;
    
}